.char-card {
    width: 200px;
    height: 320px;
    box-shadow: 5px 5px 20px rgba(0, 0, 0, 0.25);
    background: #232222;
    color: #fff;
    font-weight: 700;
    font-size: 19px;
    text-transform: uppercase;
    transition: all 0.5s ease;
    cursor: pointer;
}

.char-card.selected {
    transform: translateY(-10px);
    box-shadow: 5px 5px 20px #9f0013
}

.char-card:hover {
    transform: translateY(-10px);
    box-shadow: 5px 5px 20px #9f0013
}

.char-card__avatar {
    height: 200px;
    width: 100%;
    background: #232222;
}
.char-card__avatar img {
    object-fit: cover;
    object-position: center;
    width: 100%;
    height: 100%;
}
.char-card__descr {
    padding: 15px;
    line-height: 25.7px;
}

