.random-char {
    background: #232222;
    width: 50%;
    height: 100%;
    display: flex;
    flex-direction: column;
    padding: 35px;
    justify-content: space-between;
    position: relative;
}
.random-char::after {
    content: '';
    position: absolute;
    bottom: 10px;
    right: -50px;
    width: 203px;
    height: 189px;
    background: url(../../shared/img/mjolnir.svg);
}

.random-char__descr {
    font-size: 24px;
    font-weight: 700;
    line-height: 31.65px;
    flex: 1 1 auto;
    color: #fff;
}


