.selected-char {
    width: 50%;
    display: flex;
    padding: 35px;
    gap: 30px;
    align-items: center;
}

.selected-char img {
    object-fit: cover;
    width: 100%;
    height: 100%;
}

.selected-char__avatar {
    width: 180px;
    height: 180px;
}

.selected-char__avatar img {
    object-fit: cover;
    width: 100%;
    height: 100%;
}

.selected-char__info {
    width: 265px;
    display: flex;
    flex-direction: column;
} 
.selected-char__title {
    font-size: 22px;
    font-weight: 700;
    min-height: 22px;
}

.selected-char__descr {
    margin-top: 10px;
    font-size: 14px;
    min-height: 100px;
    height: 100%;
    width: 100%;
    line-height: 16.41px;
}
.selected-char__buttons {
    display: flex;
    gap: 50px;
    margin-top: 10px;
}

a.btn{
    color: #fff!important;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}