
.container {
    max-width: 1140px;
    width: 100%;
    margin: 0 auto;
    height: 100%;
    padding: 0 15px;
}
.char{
    height: 250px;
}
.char__container {
    display: flex;
    box-shadow: 5px 5px 20px rgba(0, 0, 0, 0.25);
}

.chars {
    margin-top: 50px;
}
.chars__container {
    display: flex;
    gap: 25px;
}

.chars__button {
    text-align: center;
    margin: 55px auto;
    width: 170px;
    padding-bottom: 55px;
}
.wrapper {
    background: url(../../shared/img/bg.png) 100% 100% no-repeat;
    z-index: -5;
}

