.char-info {
    max-width: 425px;
    width: 100%;
}

.char-info__about {
    width: 100%;
    box-shadow: 5px 5px 20px rgba(0, 0, 0, 0.25);
    height: min-content;
    padding: 25px;
}

.char-info__header {
    display: flex;
}
.char-info__title {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 6px;
    margin-left: 25px;
}

.char-info__avatar {
    height: 152px;
    flex: 0 0 152px;
}
.char-info__avatar img {
    object-fit: cover;
    width: 100%;
    height: 100%;
    object-position: center;
}

.char-info__name {
    font-size: 20px;
    text-transform: uppercase;
    font-weight: 700;
    // flex: 1 1 auto;
    align-self: center;
    min-height: 60px;
    text-align: center;
}

.char-info__buttons {
    display: flex;
    flex-direction: column;
    gap: 10px;
    align-items: center;
}

.char-info__content {
    margin-top: 15px;
    line-height: 18.41px;
    font-size: 14px;
}

.char-info__comics {
    margin-top: 10px;
    font-size: 18px;
    font-weight: 700;
    line-height: 21.09px;
}
.char-info__form {
    margin-top: 30px;
    width: 100%;
    box-shadow: 5px 5px 20px rgba(0, 0, 0, 0.25);
    padding: 25px;
}