.skeleton {
    text-align: center;

    &__title {
        font-style: normal;
        font-weight: 700;
        font-size: 18px;
        line-height: 21px;
        color: #000000;
    }

    &__body {
        margin-top: 30px;
    }

    &__top {
        display: flex;
        align-items: center;
        gap: 9px;
    }

    &__avatar {
        background: #C4C4C4;
        width: 40px;
        height: 40px;
        border-radius: 50%;
    }

    &__subtitle {
        background: #C4C4C4;
        width: 326px;
        height: 16px;
    }   

    &__descr {
        margin-top: 15px;
        background: #C4C4C4;
        height: 35px;
    }
}