.btn {
    width: 81px;
    margin-left: 10px;
    height: 38px;
    background: #9F0013;
    color: #fff;
    font-size: 14px;
    text-transform: uppercase;
    position: relative;
    font-weight: 400;
    transition: all 0.5s ease;
}

.disabled {
    filter: grayscale(0.7);
}

.btn:disabled {
    filter: grayscale(0.5);

}

.btn:hover {
    transform: translateY(-7px);
}

.gray {
    background: #5C5C5C;

}
.big {
    width: 150px;
}

.btn::after,
.btn::before {
    content: "";
    position: absolute;
    top: 0;
    bottom: 0;
}

.btn::after {
    right: 100%;
    border-right: 10px solid #9F0013;
    border-top: 10px solid transparent;
}

.btn::before {
    left: 100%;
    border-left: 10px solid #9F0013;
    border-bottom: 10px solid transparent;
}

.gray.btn::before{
    border-left: 10px solid #5C5C5C;
    border-bottom: 10px solid transparent;
}
.gray.btn::after {
    border-right: 10px solid #5C5C5C;
    border-top: 10px solid transparent;
}



